/* 1440px and below */
@media screen and (max-width: 1440px) {
  .bye-h1card-1440-inner {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 1024px) {
  .bye-h1card-welcome-to-parent {
    flex-direction: column;
  }
  .withless-logo-wrapper {
    flex: 1;
    height: 0;
    padding: 20px 0 0;
  }
  .blue-vector-bg {
    top: 600px;
  }
}

@media screen and (max-width: 768px) {
  .bye-h1card-welcome-container,
  .withless-logo-wrapper {
    font-size: 62px;
    line-height: 77px;
    min-width: 100%;
  }
  .header-content {
    gap: 17px;
  }
  .traveller-icon {
    width: 100px;
    height: auto;
  }
  .brand-description-parent {
    flex-wrap: wrap;
  }
  .itemised-content,
  .illustration-group {
    min-width: 100%;
  }
  .main-content-parent {
    gap: 22px;
  }
  .footer {
    gap: var(--gap-base);
    padding-top: 44px;
    padding-bottom: var(--padding-xl);
    box-sizing: border-box;
    margin-top: -80px;
  }
  .bye-h1card-1440 {
    gap: 38px;
  }
  .bye-h1card-welcome-to-parent {
    flex-wrap: wrap;
  }
  .header-content {
    flex-direction: column;
    align-items: center;
    gap: 34px;
  }
  .itemised-content {
    flex: 1;
    padding: 60px 0 85px;
  }
  .main-content-parent {
    flex-wrap: wrap;
  }
  .footer-content {
    gap: 130px;
    flex-wrap: wrap;
  }
  .frame-parent {
    text-align: center;
  }
  .illustration-group {
    order: -1;
  }
  .blue-vector-bg {
    display: none;
  }
  .section-content {
    background-color: var(--color-blue-100);
  }
  .button {
    align-self: center;
  }
  .frame-group {
    align-items: center;
  }
}

@media screen and (max-width: 425px) {
  .bye-h1card-welcome-container {
    font-size: 47px;
    line-height: 58px;
  }
  .were-excited-to {
    font-size: var(--font-size-lgi);
    line-height: 26px;
  }
  .frame-parent {
    gap: 15px;
  }
  .brand-description {
    gap: var(--gap-xl);
  }
  .contact1,
  .legal1 {
    font-size: var(--font-size-lgi);
    line-height: 26px;
  }
  .contact-parent {
    flex-wrap: wrap;
  }
  .footer-content {
    gap: 65px;
  }
  .bye-h1card-1440 {
    gap: 19px;
  }
}

/* 375px and below */
@media screen and (max-width: 375px) {
  .footer-content {
    flex-direction: column;
    align-items: center;
  }
  .footer-links {
    margin-top: 20px;
  }
}
